footer.main{
  position: relative;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 30px;
  padding-top: 50px!important;
  padding-bottom: 30px!important;
  &:before{
    position: absolute;
    top: 0;
    bottom: 0;
    left: -5000px;
    display: block;
    width: 10000px;
    background: var(--color-white);
    content: "";
  }
  .item{
    position: relative;
    display: flex;
    flex-direction: column;
    &:nth-child(1){
      a.logo{
        display: block;
        width: 160px;
        height: 55px;
      }
      .desc{
        padding: 30px 0 0;
      }
      .copy{
        margin: auto 0 0;
        font-size: 12px;
      }
    }
    &:nth-child(2){
      ul{
        columns: 2;
        column-gap: 40px;
        li{
          padding: 0 0 12px;
          a{
            font-size: 14px;
          }
        }
      }
      a.politic{
        margin: auto 0 0;
        font-size: 12px;
      }
    }
    &:nth-child(3){
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      a{
        display: inline-block;
        font-size: 32px;
        font-weight: 500;
        line-height: 125%;
        color: var(--color-text-hover);
        letter-spacing: -1px;
        &.email{
          margin: 6px 0 0;
        }
      }
      ul{
        display: grid;
        grid-template-columns: repeat(3, 48px);
        gap: 16px;
        width: auto;
        padding: 32px 0 50px;
        li{
          height: 48px;
          a{
            display: block;
            width: 100%;
            height: 100%;
            border: 1px solid var(--color-socials);
            border-radius: 10px;
            &:hover{
              border-color: var(--color-text-hover);
              svg{
                use{
                  fill: var(--color-text-hover);
                }
              }
            }
            svg{
              use{
                fill: var(--color-socials);
              }
            }
          }
        }
      }
      a.created{
        display: flex;
        align-items: flex-end;
        margin: auto 0 0;
        &:hover{
          svg{
            use{
              fill: var(--color-text-hover);
            }
          }
        }
        span{
          display: block;
          padding: 50px 0 0;
          font-size: 12px;
          white-space: nowrap;
        }
        svg{
          width: 121px;
          height: 14px;
          margin: 0 0 4px 3px;
          use{
            fill: var(--color-created);
          }
        }
      }
    }
  }
}